// fonts
$default-fonts: Arial, sans-serif;

@include font('GothamPro', 'GothamPro');
@include font('GothamPro-Light', 'GothamPro-Light');
@include font('GothamPro-Medium', 'GothamPro-Medium');
@include font('GothamPro-Bold', 'GothamPro-Bold');
@include font('GothamPro-Black', 'GothamPro-Black');
@include font('GothamProNarrow-Medium', 'GothamProNarrow-Medium');
@include font('GothamProNarrow-Bold', 'GothamProNarrow-Bold');

@mixin gotham {
	font-family: 'GothamPro', $default-fonts; }
@mixin gotham-light {
	font-family: 'GothamPro-Light', $default-fonts; }
@mixin gotham-medium {
	font-family: 'GothamPro-Medium', $default-fonts; }
@mixin gotham-bold {
	font-family: 'GothamPro-Bold', $default-fonts; }
@mixin gotham-black {
	font-family: 'GothamPro-Black', $default-fonts; }
@mixin gotham-narrow-medium {
	font-family: 'GothamProNarrow-Medium', $default-fonts; }
@mixin gotham-narrow-bold {
	font-family: 'GothamProNarrow-Bold', $default-fonts; }

// colors
// $black: #000

@mixin trs {
	transition: all 0.25s; }

* {
	&::-webkit-scrollbar {
		width: 4px;
		height: 4px; }
	&::-webkit-scrollbar-track {
		background-color: transparent;
		border: none; }
	&::-webkit-scrollbar-thumb {
		background-color: #aaa;
		border-radius: 2px; }
	&::-webkit-scrollbar-thumb:hover {
		background-color: #aaa; } }

/* ===================================
	 Main Styles
	 @mixin ================================== */
body {
	position: relative;
	min-width: 990px;
	@include gotham;
	font-size: 14px;
	color: #414141;
	line-height: 1.42857; }

.wrapper {
	position: relative;
	display: flex;
	flex-direction: column;
	margin: 0 auto;
	height: 100%; }

.container {
	margin-left: auto;
	margin-right: auto;
	padding-left: 15px;
	padding-right: 15px;
	@include pie-clearfix; }


/* Helpers
	----------------------------------- */
button {
	background-color: transparent; }

a:hover, a:focus {
	text-decoration: none; }

a {
	color: inherit;
	text-decoration: none; }

table {
	vertical-align: top; }

img {
	max-width: 100%;
	vertical-align: top; }

.icon {
	display: inline-block;
	vertical-align: middle; }

.text-left {
	text-align: left; }

.text-center {
	text-align: center; }

.text-right {
	text-align: right; }

.is-hidden {
	display: none; }

.vertical {
	display: inline-block;
	vertical-align: middle; }

.cfx {
	@include pie-clearfix; }

// *
// 	+selection
// 		color: #fff
// 		background-color: $red

.btn-blue {
	display: inline-block;
	padding: 13px 13px 9px;
	color: #50b3ff;
	@include gotham-medium;
	font-size: 12px;
	border: 1px solid #50b3ff;
	border-radius: 3px;
	@include trs;
	&:hover {
		color: #ffffff;
		background-color: #50b3ff; } }


/* Tabs
	----------------------------------- */
.tabs-for {
	position: relative;
	flex: 1;
	display: flex;
	flex-direction: column;
	&__item {
		flex: 1;
		display: flex;
		flex-direction: inherit;
		& + & {
			display: none;
			padding-top: 70px; } } }

.tabs-nav {
	position: absolute;
	z-index: 101;
	top: 15px;
	left: 508px;
	@include pie-clearfix;
	&__item {
		float: left;
		padding: 9px 25px 7px;
		@include gotham-medium;
		font-size: 16px;
		color: #50b3ff;
		line-height: 20px;
		border: 1px solid #50b3ff;
		border-radius: 3px 0 0 3px;
		background-color: #ffffff;
		&.is-active {
			color: #ffffff;
			background-color: #50b3ff; }
		& + & {
			padding-left: 20px;
			padding-right: 20px;
			border-radius: 0 3px 3px 0; } } }

/* Content
	----------------------------------- */
.content {
	position: relative;
	padding: 15px;
	&__pagination {
		margin-top: 50px;
		padding-bottom: 15px; }
	&_main {
		overflow: auto;
		margin-left: 493px;
		flex: 1; }
	&__search-result {
		margin-top: 30px;
		margin-bottom: 30px;
		@include gotham-medium;
		font-size: 18px;
		color: #202020;
		line-height: 1.11; } }

.pagination {
	@include pie-clearfix;
	&__item {
		float: left;
		margin: 0 7px;
		font-size: 18px;
		color: #50b3ff;
		@include trs;
		&:hover {
			color: darken(#50b3ff,15); }
		&.is-active {
			color: #202020;
			&:hover {
				color: inherit; } } } }

.map-list {
	&_single {
		padding-top: 90px; }
	&__item {
		max-width: 640px;
		& + & {
			margin-top: 30px; } }
	&_reference &__item {
		max-width: 800px; }
	&_about &__item {
		max-width: 900px; }
	&__icon-load {
		float: left;
		margin-right: 12px;
		font-size: 0;
		color: #50b3ff;
		margin-top: 5px;
		@include trs;
		&:hover {
			color: darken(#50b3ff,15); }
		.icon {
			font-size: 32px; } }
	&__content {
		overflow: hidden; }
	&__title {
		margin-bottom: 5px;
		@include gotham-medium;
		font-size: 18px;
		color: #50b3ff;
		line-height: 1.11;
		@include trs;
		&:hover {
			color: darken(#50b3ff,15); } }
	&__date {
		margin-bottom: 3px;
		color: #929292; }
	&__text-lg {
		@include gotham-medium;
		font-size: 24px;
		color: #242424;
		line-height: 1.3; }
	&__text-bold {
		@include gotham-medium;
		font-size: 18px;
		color: #252525;
		line-height: 1.3; }
	&__subtitle {
		margin-bottom: 3px;
		&-text {
			overflow: hidden;
			& + & {
				margin-top: 20px; } } }
	&__load-link {
		display: inline-block;
		margin-bottom: 15px; }
	&__color {
		position: relative;
		top: 3px;
		float: left;
		&.filter__color {
			margin-right: 6px; } } }

.map-link {
	font-size: 0;
	.icon {
		position: relative;
		top: -1px;
		display: inline-block;
		font-size: 19px;
		color: #b4b4b4;
		margin-right: 5px;
		@include trs; }
	&:hover .icon {
		color: #50b3ff; }
	&__text {
		display: inline-block;
		vertical-align: middle;
		@include gotham;
		font-size: 14px;
		color: #50b3ff;
		text-decoration: underline;
		@include trs; }
	&:hover &__text {
		color: darken(#50b3ff,15); } }

.load-link {
	font-size: 0;
	.icon {
		position: relative;
		top: -1px;
		display: inline-block;
		font-size: 32px;
		color: #50b3ff;
		margin-right: 5px;
		@include trs; }
	&__text {
		display: inline-block;
		vertical-align: middle;
		@include gotham;
		font-size: 18px;
		color: #50b3ff;
		text-decoration: underline;
		@include trs; }
	&:hover &__text,
	&:hover .icon {
		color: darken(#50b3ff,15); } }



/* ===================================
	 Header
	 @mixin ================================== */
.header {
	position: relative;
	z-index: 102;
	flex: 0 auto;
	background-color: #ffffff;
	box-shadow: 0 0 21px rgba(0, 0, 1, 0.38);
	&__container {
		position: relative;
		max-width: 1920px;
		padding-top: 25px;
		padding-bottom: 23px;
		@include r(1439) {
			padding-top: 10px;
			padding-bottom: 35px; } }
	&__logo {
		float: left;
		margin-top: 13px; }
	&__nav {
		float: right;
		margin: 20px 90px 0;
		@include r(1919) {
			margin-left: 50px;
			margin-right: 50px; }
		@include r(1599) {
			margin-left: 30px;
			margin-right: 30px; }
		@include r(1439) {
			position: absolute;
			bottom: 1px;
			left: 15px; } }
	&__user {
		float: right; }
	&__form {
		position: relative;
		margin-top: 11px;
		float: right;
		width: 425px;
		@include pie-clearfix;
		@include r(1919) {
			width: 320px; }
		@include r(1439) {
			margin-right: 30px; }
		&-input {
			width: 100%;
			margin-right: -44px;
			padding-left: 12px;
			padding-right: 50px;
			height: 38px;
			font-size: 14px;
			line-height: 38px;
			border: 1px solid #aeaeae;
			border-radius: 20px;
			@include placeholder-color(#b5b5b5); }
		&-submit {
			position: absolute;
			width: 44px;
			height: 38px;
			color: #ffffff;
			text-indent: -5px;
			background-color: #50b3ff;
			border-radius: 0 20px 20px 0;
			z-index: 1;
			@include trs;
			&:hover {
				background-color: darken(#50b3ff,10); } } } }


/* Nav
	----------------------------------- */
.nav {
	@include pie-clearfix;
	&__link {
		position: relative;
		margin: 0 20px;
		@include gotham-medium;
		font-size: 16px;
		color: #3d4a6b;
		@include trs;
		@include r(1599) {
			margin: 0 10px; }
		&:hover {
			color: #50b3ff; }
		&.is-active {
			color: #50b3ff;
			&:before {
				content: '';
				position: absolute;
				z-index: 1;
				left: 0;
				right: 0;
				bottom: -5px;
				height: 2px;
				background-color: #50b3ff; } } } }


/* User
	----------------------------------- */
.user {
	@include pie-clearfix;
	&__image {
		overflow: hidden;
		width: 50px;
		height: 50px;
		border-radius: 50%;
		float: left; }
	&__img {
		width: 100%; }
	&__name {
		float: left;
		margin-top: 18px;
		margin-left: 7px;
		color: #272727; } }


/* Logo
	----------------------------------- */
.logo {
	&__link {
		display: block;
		@include pie-clearfix; }
	&__img {
		float: left;
		margin-right: 7px;
		font-size: 36px; }
	&__text {
		float: left;
		overflow: hidden;
		color: #3d4a6b; }
	&__title {
		font-size: 10px; }
	&__subtitle {
		@include gotham-black;
		font-size: 18px;
		text-transform: uppercase; } }


/* ===================================
	 Main
	 @mixin ================================== */
.main {
	position: relative;
	overflow: auto;
	flex: 1 auto;
	flex-direction: inherit;
	display: flex; }


/* Box
	----------------------------------- */
.box {
	width: 428px;
	box-shadow: 1.5px 2.6px 13px rgba(0, 0, 1, 0.35);
	&_map {
		position: absolute;
		z-index: 100;
		margin: 0;
		top: 15px;
		left: 15px; }
	&_rounded {
		overflow: hidden;
		border-radius: 2px; }
	&__head {
		padding: 5px 45px;
		height: 155px;
		color: #ffffff;
		background: #fff url(../img/bg/box-head.jpg) center center no-repeat;
		background-size: cover;
		border-radius: 2px 2px 0 0;
		@include vertical;
		&-in {
			display: inline-block;
			vertical-align: middle;
			width: 100%; }
		&-row {
			display: table;
			table-layout: fixed;
			margin-bottom: 15px;
			width: 100%; }
		&-col {
			display: table-cell;
			vertical-align: middle;
			font-size: 14px;
			&:nth-child(1),
			&:nth-child(3) {
				font-size: 16px;
				line-height: 1.3; }
			&:nth-child(1) {
				text-align: right; }
			&:nth-child(2) {
				width: 50px;
				color: #f2b626; }
			&:nth-child(3) {
				text-align: left; } }
		&-info {
			border-bottom: 1px solid rgba(255, 255, 255, .43); }
		&-text {
			@include gotham-light;
			font-size: 14px;
			line-height: 1.29; } }
	&__about {
		padding: 25px; }
	&__main {
		background-color: #fff;
		border-radius: 0 0 2px 2px; }
	&__content {
		overflow: auto;
		display: block;
		padding: 0 10px 0 0;
		background-color: #ffffff; }
	&_rounded &__content {
		padding: 0; } }

.about {
	text-align: left;
	@include gotham;
	font-size: 16px;
	color: #242424;
	&__title {
		margin-bottom: 30px;
		@include gotham-medium;
		font-size: 24px; }
	&__label {
		@include gotham-medium;
		line-height: 1.2;
		&_md {
			font-size: 18px; } }
	&__desc {
		margin-bottom: 20px;
		line-height: 1.2;
		&_md {
			font-size: 18px; } } }

.helper-1 {
	position: relative;
	width: 470px;
	background-color: white;
	border-radius: 2px;
	padding: 20px 10px 30px 20px;
	box-shadow: 1.5px 2.6px 13px rgba(0, 0, 1, 0.35);
	&:before {
		content: '';
		position: absolute;
		z-index: 2;
		top: 0;
		margin-top: -15px;
		left: 50%;
		margin-left: -11px;
		@include arr(22,15,#fff,t); }
	&:after {
		content: '';
		position: absolute;
		z-index: 1;
		top: 0;
		margin-top: -20px;
		left: 50%;
		margin-left: -15px;
		@include arr(30,20,rgba(0, 0, 0, 0.04),t); }
	&__head {
		display: table;
		width: 100%;
		margin-bottom: 10px;
		@include pie-clearfix;
		&-image {
			display: table-cell;
			width: 109px;
			vertical-align: bottom; }
		&-content {
			display: table-cell;
			padding-left: 12px;
			vertical-align: bottom;
			@include gotham-medium;
			font-size: 16px;
			color: #212121;
			line-height: 1.25; } }
	&__map-link {
		position: relative;
		.icon {
			color: #f8595b; } }
	&__label {
		color: #929292; }
	&__value {
		margin-bottom: 10px;
		color: #212121; }
	&__btn {
		height: 36px;
		line-height: 36px;
		white-space: nowrap;
		color: #ffffff;
		display: inline-block;
		padding-left: 20px;
		padding-right: 20px;
		background-color: #50b3ff;
		border-radius: 2px;
		@include trs;
		&:hover {
			background-color: darken(#50b3ff,15); } } }

.geo {
	display: none;
	position: absolute;
	z-index: 1;
	left: 100%;
	top: 50%;
	margin-left: 15px;
	padding: 10px 15px;
	transform: translateY(-50%);
	@include gotham;
	font-size: 14px;
	color: #929292;
	line-height: 1.43;
	background-color: white;
	border: 1px solid #ddd;
	border-radius: 3px;
	box-shadow: 1px 2px 6px rgba(0, 0, 0, 0.05);
	&:before {
		content: '';
		position: absolute;
		z-index: 2;
		right: 100%;
		top: 50%;
		margin-top: -8px;
		margin-right: -1px;
		@include arr(8,16,#fff,l); }
	&:after {
		content: '';
		position: absolute;
		z-index: 1;
		right: 100%;
		top: 50%;
		margin-top: -8px;
		margin-right: 0;
		@include arr(8,16,#ddd,l); }
	&__row {
		white-space: nowrap; }
	&__number {
		color: #212121; } }


/* Filter
	----------------------------------- */
.filter {
	&__row {
		position: relative;
		border-bottom: 1px solid #d6d6d6;
		border-right: 1px solid #d6d6d6;
		@include trs;
		&.is-active {
			z-index: 2;
			box-shadow: 1.5px 2.6px 16px rgba(0, 0, 1, 0.26); }
		&-visible {
			padding: 13px 0;
			display: table;
			width: 100%; }
		&-hidden {
			display: none;
			top: 100%;
			left: 0;
			right: 0;
			padding: 30px 0 18px;
			background-color: #ffffff; }
		&_head {
			cursor: auto;
			display: table;
			width: 100%;
			padding: 9px 0; }
		&-left {
			display: table-cell;
			vertical-align: middle;
			width: 50%;
			padding: 0 18px; }
		&-right {
			display: table-cell;
			vertical-align: middle;
			width: 50%;
			padding: 0 10px;
			text-align: right; } }
	&__col {
		display: table-cell;
		vertical-align: middle;
		&:nth-child(1) {
			padding: 0 0 0 18px;
			color: #9f9f9f;
			font-size: 0;
			.icon {
				cursor: pointer; }
			.icon.is-active {
				color: #50b3ff; } }
		&:nth-child(2) {
			padding: 0 0 0 10px; }
		&:nth-child(3) {
			padding: 0 0 0 14px; }
		&:nth-child(4) {
			font-size: 0;
			text-align: center;
			padding: 0 10px; } }
	&__check {
		display: none;
		visibility: hidden;
		opacity: 0;
		&-label {
			cursor: pointer;
			position: relative;
			display: block;
			width: 18px;
			color: #202020;
			&:before {
				display: inline-block;
				vertical-align: middle;
				width: 16px;
				height: 16px;
				border: 1px solid #9f9f9f;
				border-radius: 2px;
				z-index: 1;
				content: ''; }
			&:after {
				position: absolute;
				z-index: 1;
				top: 5px;
				left: 4px;
				@include s('mark-check-correct');
				opacity: 0;
				transform: scale(0.5);
				transition: all 0.5s;
				content: ''; }
			&_text {
				display: inline-block;
				width: auto;
				&:after {
					display: inline-block;
					position: relative;
					left: auto;
					right: auto;
					top: auto;
					bottom: auto;
					vertical-align: middle;
					margin-left: 9px;
					width: 16px;
					height: 16px;
					border: 1px solid #9f9f9f;
					border-radius: 2px;
					z-index: 1;
					opacity: 1;
					transform: scale(1);
					background: none;
					content: ''; }
				&:before {
					position: absolute;
					z-index: 2;
					top: 5px;
					right: 3px;
					@include s('mark-check-correct');
					opacity: 0;
					transform: scale(0.5);
					transition: all 0.5s;
					border: none;
					border-radius: 0;
					content: ''; } } }
		&:checked + &-label {
			&:after {
				opacity: 1;
				transform: scale(1); } }
		&:checked + &-label_text {
			&:before {
				opacity: 1;
				transform: scale(1); } } }
	&__title {
		max-height: 38px;
		overflow: hidden; }
	&__row.is-active &__title {
		@include gotham-medium; }
	&__color {
		width: 10px;
		height: 10px;
		margin-left: auto;
		margin-right: auto;
		border-radius: 50%;
		background-color: #000000; }
	&__count {
		display: inline-block;
		margin-top: 7px;
		padding: 2px 0 0;
		width: 39px;
		background-color: #eee;
		font-size: 12px;
		color: #9f9f9f;
		border-radius: 10px;
		text-align: center; }
	&__row2 {
		display: table;
		width: 100%;
		position: relative;
		& + & {
			margin-top: 10px; } }
	&__col2 {
		position: relative;
		display: table-cell;
		vertical-align: middle;
		padding: 0 18px;
		& + & {
			width: 100%;
			padding: 0 10px 0 0; } }
	&__input2 {
		padding: 0 12px;
		width: 100%;
		border: 1px solid #d6d6d6;
		border-radius: 2px;
		height: 32px;
		font-size: 14px;
		line-height: 32px;
		background-color: #ffffff;
		@include placeholder-color(#929292); } }

.tagtacular_add_input {
	&.ui-autocomplete-input {
		padding: 0 12px;
		width: 100%;
		border-radius: 2px;
		height: 31px;
		font-size: 14px;
		line-height: 31px;
		background-color: transparent;
		@include placeholder-color(#929292); } }
.tagtacular_add_button {
	position: absolute;
	z-index: 2;
	top: 5px;
	right: 15px;
	width: 23px;
	height: 23px;
	@include gotham-medium;
	font-size: 20px;
	color: #ffffff;
	line-height: 26px;
	background-color: #50b3ff;
	border-radius: 3px;
	@include trs;
	&:hover {
		background-color: darken(#50b3ff,15); } }
.ui-autocomplete {
	background-color: #ffffff;
	border-radius: 3px;
	box-shadow: 1.5px 2.6px 13px rgba(0, 0, 1, 0.35);
	&.ui-menu {
		.ui-state-active {
			margin: 0; }
		.ui-menu-item {
			&:hover {
				background-color: #eeeeee; } } } }
.tagtacular_flash.tagtacular_success {
	display: none !important; }
.tagtacular_value {
	@include gotham;
	font-size: 14px;
	color: #202020;
	line-height: 22px; }
.tagtacular_tag.tagtacular_tag_edit {
	position: relative;
	float: left;
	padding-left: 4px;
	padding-right: 10px;
	background-color: #eeeeee;
	border-radius: 3px;
	margin: 4px; }
.tagtacular_tag_tray {
	position: relative;
	top: 1px;
	border-top: 1px solid #d6d6d6;
	@include pie-clearfix; }
.tagtacular_delete {
	font-size: 11px;
	position: absolute;
	top: 2px;
	right: 3px; }
.tagtacular_flash.tagtacular_failure {
	display: none !important; }
.tags {
	border-radius: 2px;
	background-color: #ffffff;
	border: 1px solid #d6d6d6; }


/* Map
	----------------------------------- */
.map {
	overflow: hidden;
	width: 100%;
	flex: 1;
	flex-direction: inherit;
	display: flex;
	iframe {
		flex: 1;
		height: auto; } }

/* Modal More
	----------------------------------- */
.modal-more {
	display: none;
	position: absolute;
	z-index: 98;
	top: 0;
	left: 0;
	right: 0;
	padding-top: 60px;
	background-color: #ffffff;
	&__close {
		position: absolute;
		z-index: 2;
		top: 45px;
		right: 15px; }
	&__in {
		max-width: 410px; }
	&__head {
		display: table;
		width: 100%;
		margin-bottom: 10px;
		@include pie-clearfix;
		&-image {
			display: table-cell;
			width: 109px;
			vertical-align: bottom; }
		&-content {
			display: table-cell;
			padding-left: 12px;
			vertical-align: bottom;
			@include gotham-medium;
			font-size: 16px;
			color: #212121;
			line-height: 1.25; } }
	&__map-link {
		.icon {
			color: #f8595b; } }
	&__label {
		color: #929292; }
	&__value {
		margin-bottom: 10px;
		color: #212121; } }

/* Filter2
	----------------------------------- */
.filter2 {
	&__section {
		padding: 20px;
		border-bottom: 1px solid #d6d6d6; }
	&__row {
		position: relative;
		margin-bottom: 20px;
		@include pie-clearfix;
		& + & {
			margin-top: 10px;
			margin-bottom: 0;
			&_date {
				margin-top: 20px; } }
		&-left {
			float: left;
			width: 50%; }
		&-right {
			float: right;
			width: 50%;
			text-align: right; } }
	&__title-main {
		margin-top: 10px;
		margin-bottom: 10px;
		@include gotham-medium;
		font-size: 18px;
		color: #202020;
		line-height: 1.11; }
	&__radio {
		display: none;
		visibility: hidden;
		&-label {
			cursor: pointer;
			position: relative;
			padding-left: 25px;
			color: #202020;
			&:before {
				position: absolute;
				z-index: 1;
				left: 0;
				top: -2px;
				width: 13px;
				height: 13px;
				background-color: #ffffff;
				border: 1px solid #9d9d9d;
				border-radius: 50%;
				content: ''; }
			&:after {
				position: absolute;
				z-index: 2;
				left: 4px;
				top: 2px;
				width: 7px;
				height: 7px;
				opacity: 0;
				border-radius: 100%;
				background-color: #50b3ff;
				transition: all 0.5s;
				transform: scale(0.5);
				content: ''; } }
		&:checked + &-label {
			&:after {
				opacity: 1;
				transform: scale(1); } } } }

.date {
	@include pie-clearfix;
	&__label {
		float: left;
		width: 30px;
		@include gotham;
		font-size: 14px;
		color: #202020;
		line-height: 32px; }
	&__input-style {
		position: relative;
		float: left;
		width: 220px;
		.icon {
			position: absolute;
			right: 10px;
			top: 7px;
			z-index: 1;
			pointer-events: none;
			font-size: 17px;
			color: #c5c5c5; } }
	&__input {
		padding: 0 40px 0 12px;
		width: 100%;
		height: 32px;
		font-size: 14px;
		color: #202020;
		line-height: 32px;
		background-color: white;
		border: 1px solid #d6d6d6;
		border-radius: 1px;
		@include placeholder-color(#929292); } }


/* ===================================
	 Other Styles
	 @mixin ================================== */
.ui-datepicker {
	padding: 0;
	background-color: transparent;
	border: none;
	box-shadow: 1.5px 2.6px 13px rgba(0, 0, 1, 0.35);
	.ui-widget-header {
		@include gotham;
		border-radius: 3px 3px 0 0;
		color: #ffffff;
		border: none;
		background: #57b4fc; }
	.ui-datepicker-prev,
	.ui-datepicker-next {
		cursor: pointer;
		background-color: transparent;
		border: none; }
	.ui-datepicker-prev {
		margin-left: 2px;
		left: 2px;
		top: auto;
		&:hover {
			background-color: transparent;
			border: none;
			left: 2px;
			top: auto; }
		&:before {
			content: '';
			position: absolute;
			@include vcenter;
			z-index: 1;
			@include arr(6,10,#ffffff,l); } }
	.ui-datepicker-next {
		margin-right: 2px;
		right: 2px;
		top: auto;
		&:hover {
			background-color: transparent;
			border: none;
			right: 2px;
			top: auto; }
		&:before {
			content: '';
			position: absolute;
			@include vcenter;
			z-index: 1;
			@include arr(6,10,#ffffff,r); } }
	.ui-datepicker-calendar {
		background-color: #ffffff;
		margin: 0; }
	td {
		padding: 2px 0;
		border-right: 1px solid #ccc;
		&:last-child {
			border-right: none; } }
	th {
		padding: 2px 0;
		border-right: 1px solid #ccc;
		&:last-child {
			border-right: none; } }
	.ui-state-default {
		@include gotham;
		text-align: center;
		color: #4b4b4b;
		background: none;
		border: none;
		&:hover {
			background-color: lighten(#57b4fc,15);
			color: #ffffff; } }
	.ui-state-highlight {
		background-color: lighten(#57b4fc,15);
		color: #ffffff; }
	.ui-state-active {
		background-color: #57b4fc;
		color: #ffffff; }
	.ui-datepicker-unselectable {
		.ui-state-default {
			color: lighten(#d5cfcc,5); } } }
