.filter__check-label { cursor: pointer; position: relative; display: block; width: 18px; color: #202020; }

.filter__check-label:before { display: inline-block; vertical-align: middle; width: 16px; height: 16px; border: 1px solid #9f9f9f; border-radius: 2px; z-index: 1; content: ''; }

.filter__check-label:after { position: absolute; z-index: 1; top: 5px; left: 4px; background-image: url("../img/icons.png"); width: 11px; height: 9px; background-position: 0px 0px; opacity: 0; transform: scale(0.5); transition: all 0.5s; content: ''; }

.filter__check-label_text { display: inline-block; width: auto; }

.filter__check-label_text:after { display: inline-block; position: relative; left: auto; right: auto; top: auto; bottom: auto; vertical-align: middle; margin-left: 9px; width: 16px; height: 16px; border: 1px solid #9f9f9f; border-radius: 2px; z-index: 1; opacity: 1; transform: scale(1); background: none; content: ''; }

.filter__check-label_text:before { position: absolute; z-index: 2; top: 5px; right: 3px; background-image: url("../img/icons.png"); width: 11px; height: 9px; background-position: 0px 0px; opacity: 0; transform: scale(0.5); transition: all 0.5s; border: none; border-radius: 0; content: ''; }

.filter__check:checked + .filter__check-label:after { opacity: 1; transform: scale(1); }

.filter__check:checked + .filter__check-label_text:before { opacity: 1; transform: scale(1); }

.filter__title { max-height: 38px; overflow: hidden; }

.filter__row.is-active .filter__title { font-family: "GothamPro-Medium", Arial, sans-serif; }

.filter__color { width: 10px; height: 10px; margin-left: auto; margin-right: auto; border-radius: 50%; background-color: #000000; }

.filter__count { display: inline-block; margin-top: 7px; padding: 2px 0 0; width: 39px; background-color: #eee; font-size: 12px; color: #9f9f9f; border-radius: 10px; text-align: center; }

.filter__row2 { display: table; width: 100%; position: relative; }

.filter__row2 + .filter__row2 { margin-top: 10px; }

.filter__col2 { position: relative; display: table-cell; vertical-align: middle; padding: 0 18px; }

.filter__col2 + .filter__col2 { width: 100%; padding: 0 10px 0 0; }

.filter__input2 { padding: 0 12px; width: 100%; border: 1px solid #d6d6d6; border-radius: 2px; height: 32px; font-size: 14px; line-height: 32px; background-color: #ffffff; }

.filter__input2:-moz-placeholder, .filter__input2::-moz-placeholder { color: #929292; opacity: 1; }

.filter__input2::-webkit-input-placeholder { color: #929292; }

.tagtacular_add_input.ui-autocomplete-input { padding: 0 12px; width: 100%; border-radius: 2px; height: 31px; font-size: 14px; line-height: 31px; background-color: transparent; }

.tagtacular_add_input.ui-autocomplete-input:-moz-placeholder, .tagtacular_add_input.ui-autocomplete-input::-moz-placeholder { color: #929292; opacity: 1; }

.tagtacular_add_input.ui-autocomplete-input::-webkit-input-placeholder { color: #929292; }

.tagtacular_add_button { position: absolute; z-index: 2; top: 5px; right: 15px; width: 23px; height: 23px; font-family: "GothamPro-Medium", Arial, sans-serif; font-size: 20px; color: #ffffff; line-height: 26px; background-color: #50b3ff; border-radius: 3px; transition: all 0.25s; }

.tagtacular_add_button:hover { background-color: #0492ff; }

.ui-autocomplete { background-color: #ffffff; border-radius: 3px; box-shadow: 1.5px 2.6px 13px rgba(0, 0, 1, 0.35); }

.ui-autocomplete.ui-menu .ui-state-active { margin: 0; }

.ui-autocomplete.ui-menu .ui-menu-item:hover { background-color: #eeeeee; }

.tagtacular_flash.tagtacular_success { display: none !important; }

.tagtacular_value { font-family: "GothamPro", Arial, sans-serif; font-size: 14px; color: #202020; line-height: 22px; }

.tagtacular_tag.tagtacular_tag_edit { position: relative; float: left; padding-left: 4px; padding-right: 10px; background-color: #eeeeee; border-radius: 3px; margin: 4px; }

.tagtacular_tag_tray { position: relative; top: 1px; border-top: 1px solid #d6d6d6; }

.tagtacular_tag_tray:after { content: ""; display: table; clear: both; }

.tagtacular_delete { font-size: 11px; position: absolute; top: 2px; right: 3px; }

.tagtacular_flash.tagtacular_failure { display: none !important; }

.tags { border-radius: 2px; background-color: #ffffff; border: 1px solid #d6d6d6; }

/* Map ----------------------------------- */
.map { overflow: hidden; width: 100%; flex: 1; flex-direction: inherit; display: flex; }

.map iframe { flex: 1; height: auto; }

/* Modal More ----------------------------------- */
.modal-more { display: none; position: absolute; z-index: 98; top: 0; left: 0; right: 0; padding-top: 60px; background-color: #ffffff; }

.modal-more__close { position: absolute; z-index: 2; top: 45px; right: 15px; }

.modal-more__in { max-width: 410px; }

.modal-more__head { display: table; width: 100%; margin-bottom: 10px; }

.modal-more__head:after { content: ""; display: table; clear: both; }

.modal-more__head-image { display: table-cell; width: 109px; vertical-align: bottom; }

.modal-more__head-content { display: table-cell; padding-left: 12px; vertical-align: bottom; font-family: "GothamPro-Medium", Arial, sans-serif; font-size: 16px; color: #212121; line-height: 1.25; }

.modal-more__map-link .icon { color: #f8595b; }

.modal-more__label { color: #929292; }

.modal-more__value { margin-bottom: 10px; color: #212121; }

/* Filter2 ----------------------------------- */
.filter2__section { padding: 20px; border-bottom: 1px solid #d6d6d6; }

.filter2__row { position: relative; margin-bottom: 20px; }

.filter2__row:after { content: ""; display: table; clear: both; }

.filter2__row + .filter2__row { margin-top: 10px; margin-bottom: 0; }

.filter2__row + .filter2__row_date { margin-top: 20px; }

.filter2__row-left { float: left; width: 50%; }

.filter2__row-right { float: right; width: 50%; text-align: right; }

.filter2__title-main { margin-top: 10px; margin-bottom: 10px; font-family: "GothamPro-Medium", Arial, sans-serif; font-size: 18px; color: #202020; line-height: 1.11; }

.filter2__radio { display: none; visibility: hidden; }

.filter2__radio-label { cursor: pointer; position: relative; padding-left: 25px; color: #202020; }

.filter2__radio-label:before { position: absolute; z-index: 1; left: 0; top: -2px; width: 13px; height: 13px; background-color: #ffffff; border: 1px solid #9d9d9d; border-radius: 50%; content: ''; }

.filter2__radio-label:after { position: absolute; z-index: 2; left: 4px; top: 2px; width: 7px; height: 7px; opacity: 0; border-radius: 100%; background-color: #50b3ff; transition: all 0.5s; transform: scale(0.5); content: ''; }

.filter2__radio:checked + .filter2__radio-label:after { opacity: 1; transform: scale(1); }

.date:after { content: ""; display: table; clear: both; }

.date__label { float: left; width: 30px; font-family: "GothamPro", Arial, sans-serif; font-size: 14px; color: #202020; line-height: 32px; }

.date__input-style { position: relative; float: left; width: 220px; }

.date__input-style .icon { position: absolute; right: 10px; top: 7px; z-index: 1; pointer-events: none; font-size: 17px; color: #c5c5c5; }

.date__input { padding: 0 40px 0 12px; width: 100%; height: 32px; font-size: 14px; color: #202020; line-height: 32px; background-color: white; border: 1px solid #d6d6d6; border-radius: 1px; }

.date__input:-moz-placeholder, .date__input::-moz-placeholder { color: #929292; opacity: 1; }

.date__input::-webkit-input-placeholder { color: #929292; }

/* =================================== Other Styles @mixin ================================== */
.ui-datepicker { padding: 0; background-color: transparent; border: none; box-shadow: 1.5px 2.6px 13px rgba(0, 0, 1, 0.35); }

.ui-datepicker .ui-widget-header { font-family: "GothamPro", Arial, sans-serif; border-radius: 3px 3px 0 0; color: #ffffff; border: none; background: #57b4fc; }

.ui-datepicker .ui-datepicker-prev, .ui-datepicker .ui-datepicker-next { cursor: pointer; background-color: transparent; border: none; }

.ui-datepicker .ui-datepicker-prev { margin-left: 2px; left: 2px; top: auto; }

.ui-datepicker .ui-datepicker-prev:hover { background-color: transparent; border: none; left: 2px; top: auto; }

.ui-datepicker .ui-datepicker-prev:before { content: ''; position: absolute; position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%); z-index: 1; width: 0px; height: 0px; border-style: solid; border-width: 5px 6px 5px 0; border-color: transparent #ffffff transparent transparent; }

.ui-datepicker .ui-datepicker-next { margin-right: 2px; right: 2px; top: auto; }

.ui-datepicker .ui-datepicker-next:hover { background-color: transparent; border: none; right: 2px; top: auto; }

.ui-datepicker .ui-datepicker-next:before { content: ''; position: absolute; position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%); z-index: 1; width: 0px; height: 0px; border-style: solid; border-width: 5px 0 5px 6px; border-color: transparent transparent transparent #ffffff; }

.ui-datepicker .ui-datepicker-calendar { background-color: #ffffff; margin: 0; }

.ui-datepicker td { padding: 2px 0; border-right: 1px solid #ccc; }

.ui-datepicker td:last-child { border-right: none; }

.ui-datepicker th { padding: 2px 0; border-right: 1px solid #ccc; }

.ui-datepicker th:last-child { border-right: none; }

.ui-datepicker .ui-state-default { font-family: "GothamPro", Arial, sans-serif; text-align: center; color: #4b4b4b; background: none; border: none; }

.ui-datepicker .ui-state-default:hover { background-color: #a2d6fd; color: #ffffff; }

.ui-datepicker .ui-state-highlight { background-color: #a2d6fd; color: #ffffff; }

.ui-datepicker .ui-state-active { background-color: #57b4fc; color: #ffffff; }

.ui-datepicker .ui-datepicker-unselectable .ui-state-default { color: #e1dcda; }
